import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
//import { myFetcher } from './fetchAction';
import { getEntries, postEntry } from './contentful.js';

const CommentsComponent2 = () => {

	const dispatch = useDispatch();
	const currentApp = useSelector(state => state.app.currentApp);
	const hasData = useSelector(state => state.app.appsList[currentApp].hasData);
	const isOnline = true;//(!navigator.onLine) ? false : true;

	/// Post/Save comment to DB.
	const postComment = async (e) => {

		// Prepare send content.
		const textArea = (e.target.previousElementSibling) ? e.target.previousElementSibling : e.target.parentNode.previousElementSibling
		const textStr = textArea.value
		if(textStr === '') return;
		const date = new Date();
		const user = 'anon'

		// Post (send) new entry to contentful.
		const response = await postEntry({textStr, date, user});

		// Evaluate response.
		if(response.sys.id){
			const newObj = {sys:response.sys, fields:{strText:textStr, dateCreated:date.toString().substring(0,24), createdBy:user}}
			const newArr = [...hasData.items, newObj]
			const newHasData = {...hasData, items: newArr}

			dispatch({ type: 'APP_GOT_DATA', payload: newHasData  });
			textArea.value = ''
		} else {
			console.log(response);
			alert('upload failed');
		}

	}

	/// No internet - abort.
	if( !isOnline ){
		return ( <div id='comments' className=''>No internet connection.</div>)
	}

	/// Fetching data.
	if( ! hasData ){

		/// Fetch data and dispatch response to store.
		(async () => {
						//async function getContentfulEntries(){
							//return myContentful()
						//}
				const response = await getEntries();
				dispatch({ type: 'APP_GOT_DATA', payload: response } );
		})()

		/// While fetching...
		return (<div id='comments' >Loading...</div>)
	}

	/// Request returned error.
	if( hasData === 'Error' ){
		/** TODO: in here - load dummy data? */
		return (<div id='comments' >Error loading content.</div>)
	}

	/// Data OK - populate component content.
	if( hasData !== 'Error'){
		const retrievedArray = hasData.items;
		const sortedItems =  retrievedArray.sort((a, b) => {
			return Date.parse(new Date(b.fields.dateCreated)) - Date.parse(new Date(a.fields.dateCreated))
		})

		const itemsList = sortedItems.map((item, i) => {
			const d = new Date(item.fields.dateCreated);
			const formattedDate = d.toUTCString().substring(0, d.toUTCString().length -4);

			return(
				<div key={i} className="commentBox" >
					<span style={{fontWeight:"bold"}}>{item.fields.createdBy} : </span>
					<span style={{color:"dodgerblue"}}> {formattedDate}</span><br />
					<div style={{paddingLeft:"0px", paddingTop:"2px"}}>{item.fields.strText}</div>
				</div>
			)
		})

		return (
			<div id='comments' >

				<div id="newComments">
					<textarea id="addCommentTextArea" style={{ height:"100px"}} placeholder="Add a comment ..."></textarea>
					<span className="right green-text text-darken-2" id="postComment" onClick={postComment}>
						<i className="material-icons waves-effect  waves-light" >send</i>
					</span>
				</div>

				{ itemsList }
			</div>
			)
	}

}

export default CommentsComponent2


