import React from 'react';
import { useSelector, useDispatch } from 'react-redux';

const RestDBCommentsComponent =  () => {
	const dispatch = useDispatch();
	const currentApp = useSelector(state => state.app.currentApp);
	const hasData = useSelector(state => state.app.appsList[currentApp].hasData);
	const GetURL = 'https://dbase-a3a5.restdb.io/rest/miscellaneous-first-items-collection'
	const XAPIkey = '5e7b60baf96f9f072a0b094d'
	const isOnline = true;//(!navigator.onLine) ? false : true;

	/// Post/Save comment to DB.
	const postComment = async (e) => {
		const textArea = (e.target.previousElementSibling) ? e.target.previousElementSibling : e.target.parentNode.previousElementSibling

		if(textArea.value === '') return;

		const textStr = textArea.value
		const date = new Date().toString().substring(0,24)
		const user = 'anon';

		/// Fetch (Post data to URL)
		(async () => {
				async function getRestDBio(){
					const URL = GetURL
					const options = {
						method: "POST",
						headers: {
							"content-type": "application/json",
							"x-apikey": XAPIkey,
							"cache-control": "no-cache" ,
							"Accept": "application/json"
						},
						body: JSON.stringify({ description: textStr, title:date, createdBy:user})
					}
					const rawSend = await fetch(URL, options);
					return await rawSend.json();
				}

				const resjson = await getRestDBio();

				if(resjson.description === textStr){
					//console.log('SUCCESS', resjson);
					const newEntry = { _id:resjson._id, id:resjson.id, description: textStr, title:date, createdBy:user}
					const newHasData = [...hasData, newEntry ]
					dispatch({ type: 'APP_GOT_DATA', payload: newHasData } );
					textArea.value = ''
				} else {
					alert("Not Saved !!")
				}
		})()
	}

	/// No internet - abort.
	if(!isOnline){
		return ( <div id='comments' className=''>No internet connection.</div>)
	}

	/// Fetching data.
	if( ! hasData ){
		//myFetcher(GetURL, dispatch)

		/// Fetch
		(async () => {
				async function getRestDBio(){
					const URL = GetURL
					const options = {
						method: "GET",
						headers: { "content-type": "application/json", "x-apikey": XAPIkey, "cache-control": "no-cache" , "Accept": "application/json"}
					}
					const rawSend = await fetch(URL, options);
					return await rawSend.text();
				}
				const response = await getRestDBio();
				const resjson = await JSON.parse(response);
				dispatch({ type: 'APP_GOT_DATA', payload: resjson } );
		})()

		return (<div id='comments' >Loading...</div>)
	}

	/// Request returned error.
	if( hasData === 'Error' ){
		/** TODO: in here - load dummy data? */
		return (<div id='comments' >Error loading content.</div>)
	}

	/// Data OK - populate component content.
	const sortedArray =  hasData.sort((a, b) => {	return b.id - a.id })
	const itemsList = sortedArray.map((item, i) => {
		let d = new Date(item.date);
		if(String(d) === 'Invalid Date'){ d = new Date(item.title); }
		if(String(d) === 'Invalid Date'){ d = new Date('2020-03-04 15:36'); }
		const formattedDate = d.toUTCString().substring(0, d.toUTCString().length -4);
		let u = item.createdBy
		if(!u || u === '' || u === 'undefined') u = '(no name)'

		return(
			<div key={i} className="commentBox" >

				<span style={{fontWeight:"bold"}}>{u} : </span>
				<span style={{color:"dodgerblue"}}> {formattedDate}</span><br />
				<div className='desc'>{item.description}</div>

			</div>
		)
	})

	return (
		<div id='comments' >

			<div id="newComments">
				<textarea id="addCommentTextArea" style={{ height:"100px"}} placeholder="Add a comment ..."></textarea>
				<span className="right green-text text-darken-2" id="postComment" onClick={postComment}>
					<i className="material-icons waves-effect  waves-light" >send</i>
				</span>
			</div>

			{ itemsList }

		</div>
		)

}

export default RestDBCommentsComponent



